import Vue from 'vue'
import VueRouter from 'vue-router'

const login = () => import(/* webpackChunkName: 'login_home_welcome' */ '../components/login.vue')
// import login from '../components/login.vue'
const home = () => import(/* webpackChunkName: 'login_home_welcome' */ '../components/home.vue')
// import home from '../components/home.vue'
const welcome = () => import(/* webpackChunkName: 'login_home_welcome' */ '../components/welcome.vue')
// import welcome from '../components/welcome.vue'

// import users from '../components/user/users.vue'
const users = () => import(/* webpackChunkName: 'users_rights_roles' */ '../components/user/users.vue')
// import rights from '../components/power/rights.vue'
const rights = () => import(/* webpackChunkName: 'users_rights_roles' */ '../components/power/rights.vue')
// import roles from '../components/power/roles.vue'
const roles = () => import(/* webpackChunkName: 'users_rights_roles' */ '../components/power/roles.vue')

// import cate from '../components/goods/cate.vue'
const cate = () => import(/* webpackChunkName: 'cate_params' */ '../components/goods/cate.vue')
// import params from '../components/goods/params.vue'
const params = () => import(/* webpackChunkName: 'cate_params' */ '../components/goods/params.vue')

// import goods from '../components/goods/goods.vue'
const goods = () => import(/* webpackChunkName: 'goods_add' */ '../components/goods/goods.vue')
// import add from '../components/goods/add.vue'
const add = () => import(/* webpackChunkName: 'goods_add' */ '../components/goods/add.vue')

// import order from '../components/order/order.vue'
const order = () => import(/* webpackChunkName: 'order_report' */ '../components/order/order.vue')
// import report from '../components/report/report.vue'
const report = () => import(/* webpackChunkName: 'order_report' */ '../components/report/report.vue')

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '/home',
      redirect: '/welcome',
      component: home,
      children: [
        {
          path: '/welcome',
          component: welcome
        },
        {
          path: '/users',
          component: users
        },
        {
          path: '/rights',
          component: rights
        },
        {
          path: '/roles',
          component: roles
        },
        {
          path: '/categories',
          component: cate
        },
        {
          path: '/params',
          component: params
        },
        {
          path: '/goods',
          component: goods
        },
        {
          path: '/goods/add',
          component: add
        },
        {
          path: '/orders',
          component: order
        },
        {
          path: '/reports',
          component: report
        }
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.path === '/login') return next()
  const loginToken = window.sessionStorage.getItem('token')
  if (!loginToken) return next('/login')
  next()
})

export default router
